import './App.css';
import {Header} from "./Header";
import {Body} from "./Body";
import {useState} from "react";
import {AFK_STORAGE, COMPUTE_PARTIALS_STORAGE, LEVEL_STORAGE} from "./utils/storage";

const App = () => {
    const [level, setLevel] = useState(LEVEL_STORAGE.get());
    const [afkHours, setAfkHours] = useState(AFK_STORAGE.get());
    const [computePartials, setComputePartials] = useState(COMPUTE_PARTIALS_STORAGE.get())

    const handleSetLevel = (newLevel) => {
        LEVEL_STORAGE.set(null, newLevel);
        setLevel(newLevel);
    };

    const handleSetAfkHours = (newAfkHours) => {
        AFK_STORAGE.set(null, newAfkHours);
        setAfkHours(newAfkHours);
    }

    const handleSetComputePartials = (newComputePartials) => {
        COMPUTE_PARTIALS_STORAGE.set(null, newComputePartials);
        setComputePartials(newComputePartials);
    }

  return (
      <>
          <Header
              level={level}
              setLevel={handleSetLevel}
              afkHours={afkHours}
              setAfkHours={handleSetAfkHours}
              computePartials={computePartials}
              setComputePartials={handleSetComputePartials}
          />
          <Body level={level} afkHours={afkHours} computePartials={computePartials} />
      </>
  );
}

export default App;
