import {IconButton, InputAdornment, TextField} from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";
import * as React from "react";

export const NumericInput = ({label, value, increment, onChange, variant, fontSize}) => {
    increment = Math.abs(increment || 1);
    fontSize = fontSize || 'medium'
    return (
        <div className="increment" style={{fontSize: fontSize}}>
            <TextField
                type="number"
                label={label}
                inputProps={{ inputMode: 'numeric', pattern: '[0-9]*'}}
                InputProps={{
                    startAdornment: <Decrement value={value} increment={increment} onChange={onChange} />,
                    endAdornment: <Increment value={value} increment={increment} onChange={onChange} />,
                }}
                size="small"
                variant={variant || "standard"}
                style={{width: "9rem"}}
                value={value}
                onChange={onChange}
            />
        </div>
    )
}

const size = 'small';

const Increment = (props) => (
    <InputAdornment position="end">
        <Adornment modifier={props.increment} {...props}>
            <AddIcon fontSize="inherit" />
        </Adornment>
    </InputAdornment>
);

const Decrement = (props) => (
    <InputAdornment position="start">
        <Adornment modifier={-props.increment} {...props}>
            <RemoveIcon fontSize="inherit" />
        </Adornment>
    </InputAdornment>
);

const Adornment = ({value, modifier, onChange, children}) => {
    value = value ? parseFloat(value) : '';
    return (
        <IconButton size={size} onClick={() => onChange({target: {value: value + modifier}})}>
            {children}
        </IconButton>
    )

};