import {
    DEFAULT_AFK_HOURS,
    DEFAULT_COMPUTE_PARTIALS,
    DEFAULT_FARM_PASS_SLOTS,
    DEFAULT_LEVEL,
    DEFAULT_SLOTS,
    DEFAULT_STARRED
} from "./constants";

const generateStorage = (prefix, defaultValue, coerce) => {
    return {
        get: (name) => coerce(localStorage.getItem(prefix + (name || '')) || defaultValue),
        set: (name, value) => localStorage.setItem(prefix + (name || ''), value),
        remove: (name) => localStorage.removeItem(prefix + (name || ''))
    }
}

const coerceBoolean = (value) => value === 'true'
const coerceInt = (value) => value ? parseInt(value) : 0;
const coerceFloat = (value) => value ? parseFloat(value) : 0.0;

export const SLOT_STORAGE = generateStorage("slots_", DEFAULT_SLOTS, coerceInt);
export const FP_SLOT_STORAGE = generateStorage("fp_slots_", DEFAULT_FARM_PASS_SLOTS, coerceInt);
export const STARRED_STORAGE = generateStorage("starred_", DEFAULT_STARRED, coerceBoolean);

export const LEVEL_STORAGE = generateStorage("level", DEFAULT_LEVEL, coerceInt);
export const AFK_STORAGE = generateStorage("afk", DEFAULT_AFK_HOURS, coerceFloat);
export const COMPUTE_PARTIALS_STORAGE = generateStorage("computePartials", DEFAULT_COMPUTE_PARTIALS, coerceBoolean);