import {PRODUCERS} from "./data";
import {Row} from "./utils/Row";
import {useEffect, useState, Fragment} from "react";
import {
    FP_SLOT_STORAGE,
    SLOT_STORAGE,
    STARRED_STORAGE
} from "./utils/storage";
import {NumericInput} from "./utils/NumericInput";
import {Button, Divider, IconButton} from "@mui/material";
import StarBorderIcon from '@mui/icons-material/StarBorder';
import StarIcon from '@mui/icons-material/Star';

export const Results = (props) => {
    return (
        <Row>
            {
                Object.keys(PRODUCERS).filter(
                    (producerKey) => PRODUCERS[producerKey].levels.length && PRODUCERS[producerKey].goods.length && PRODUCERS[producerKey].levels[0] <= props.level
                ).map((producerKey) => <Producer producerKey={producerKey} {...props} key={producerKey} />)
            }
        </Row>
    )
}

const calculateProfit = (good, starred, slots, hours, computePartials) => {
    const duration = starred ? good.star_duration_mins : good.normal_duration_mins;
    let num = Math.min((hours * 60.0) / duration, slots);
    if(!computePartials)
        num = Math.floor(num);

    const profit = num * good.value;
    return profit;
}

const Producer = (props) => {
    const {level, afkHours, computePartials, producerKey} = props;
    const [starred, setStarred] = useState(STARRED_STORAGE.get(producerKey));
    const [slots, setSlots] = useState(SLOT_STORAGE.get(producerKey));
    const [fpSlots, setFpSlots] = useState(FP_SLOT_STORAGE.get(producerKey));
    const [producer, setProducer] = useState({});
    const [goods, setGoods] = useState([]);
    const [showAll, setShowAll] = useState(false);

    useEffect(() => {
        setProducer(PRODUCERS[props.producerKey]);
    }, [props])

    useEffect(() => {
        if(producer.name) {
            setGoods(producer.goods.filter((good) => good.level && good.level <= level).map(
                (good) => { return {...good, calculatedProfit: calculateProfit(good, starred, slots, afkHours, computePartials)}}
            ).sort((a, b) => b.calculatedProfit - a.calculatedProfit));
        }
    }, [props, producer, starred, slots, fpSlots, afkHours, computePartials, level]);

    const handleChangeSlots = (newSlots) => {
        SLOT_STORAGE.set(producerKey, parseInt(newSlots));
        setSlots(newSlots);
    };

    const handleChangeFpSlots = (newFpSlots) => {
        FP_SLOT_STORAGE.set(producerKey, parseInt(newFpSlots));
        setFpSlots(newFpSlots);
    };

    const handleChangeStarred = () => {
        const newStarred = !starred;
        STARRED_STORAGE.set(producerKey, newStarred);
        setStarred(newStarred);
    };

    return !producer.name ? null : (
        <div className='producer'>
            <div className='pictureTableRow'>
                <IconButton style={{color: "gold"}} onClick={handleChangeStarred}>
                    {starred ? <StarIcon /> : <StarBorderIcon />}
                </IconButton>
                <h3>{producer.name}</h3>
            </div>
            <div className='flexContainer mayWrap' style={{justifyContent: 'space-between'}}>
                <a href={producer.link} title={producer.name}>
                    <img src={producer.image_link} height='135px' width='135px' alt={producer.name} />
                </a>
                <div className='verticalFlexContainer innerRow columnMayWrap'>
                    <Slots label="Permanent Slots" slots={slots} handleChangeHook={handleChangeSlots}/>
                    <Slots label="Farm Pass Slots" slots={fpSlots} handleChangeHook={handleChangeFpSlots}/>
                </div>
            </div>
            {
                (showAll ? goods : goods.slice(0, 3)).map((good) => (
                    <Fragment key={good.name}>
                        <div className='pictureTableRow mayWrap' style={{justifyContent: 'space-between', textWrap: 'nowrap'}}>
                            <div className='pictureTableRow' >
                                <a href={good.link} title={good.name}>
                                    <img src={good.image_link} height="30px" width="30px" alt={good.name} />
                                </a>
                                {good.name}:
                            </div>
                            <div className='pictureTableRow borderBetween'>
                                <p style={{marginLeft: "2vh", marginRight: '1vh'}}>{Math.round(good.calculatedProfit)}</p>
                                <img
                                    src="https://static.wikia.nocookie.net/hayday/images/f/f0/Coins.png"
                                    title="Coins"
                                    alt="Coins"
                                    height="15px"
                                    width="15px"
                                />
                            </div>
                        </div>
                        <Divider />
                    </Fragment>
                ))
            }
            {
                goods.length <= 3 ? null : showAll ? (
                    <Button variant="text" onClick={() => setShowAll(false)}>Show less</Button>
                ) : (
                    <Button variant="text" onClick={() => setShowAll(true)}>Show more</Button>
                )
            }
        </div>
    )
}

const Slots = ({label, slots, handleChangeHook}) => {
    const handleChange = (event) => {
        let newValue = event.target.value;

        if(!newValue || newValue < 0) {
            newValue = 0;
        }

        if(newValue !== slots) {
            handleChangeHook(newValue)
        }
    };

    return <NumericInput variant="standard" fontSize="12px" label={label} value={slots} onChange={handleChange} />

};