import {Selections} from "./Selections";

export const Header = (props) => {
    const imgDim = '40vh';
    return (
        <header>
            <div className='logo'>
                <img
                    src="https://static.wikia.nocookie.net/hayday/images/d/d6/Chicken_Hungry.png"
                    width={imgDim}
                    height={imgDim}
                    alt=""
                />
                <span>Hay Day Optimizer</span>
            </div>
            <Selections {...props} />
        </header>
    )
}