import * as React from 'react';
import {Drawer, FormControlLabel, IconButton, Switch} from "@mui/material";
import {NumericInput} from "./utils/NumericInput";
import {useLayoutEffect, useState} from "react";
import {SELECTIONS_SHRINK_THRESHOLD} from "./utils/constants";
import FilterListIcon from '@mui/icons-material/FilterList';
export const Selections = (props) => {
    const [width, setWidth] = useState(0);

    useLayoutEffect(() => {
        function updateSize() {
            setWidth(document.documentElement.clientWidth);
        }
        window.addEventListener('resize', updateSize);
        updateSize();
        return () => window.removeEventListener('resize', updateSize);
    }, []);

    return width < SELECTIONS_SHRINK_THRESHOLD ?  <HiddenSelections {...props} /> : <FullSizeSelections {...props} />
};

const HiddenSelections = (props) => {
    const [open, setOpen] = useState(false);

    return (
        <>
            <IconButton style={{color: 'white'}} onClick={() => setOpen(!open)}>
                <FilterListIcon />
            </IconButton>
            <Drawer
                anchor='right' open={open}
                onClose={() => setOpen(false)}
                sx={{
                    flexShrink: 0,
                    [`& .MuiDrawer-paper`]: { boxSizing: 'border-box' },
                }}
            >
                <div className='verticalFlexContainer innerDrawer' style={{justifyContent: 'space-between', height: '100%'}}>
                    <div>
                        <LevelInput {...props} />
                        <AfkInput {...props} />
                    </div>
                    <ComputePartialsInput {...props} labelPlacement="bottom"/>
                </div>
            </Drawer>
        </>
    )
}

const FullSizeSelections = (props) => (
    <>
        <div className="textSelections">
            <LevelInput {...props} />
            <AfkInput {...props} />
        </div>
        <div className='partialGoodsContainer'>
            <ComputePartialsInput {...props} />
        </div>
    </>
)

const LevelInput = ({level, setLevel}) => {
    const [value, setValue] = React.useState(level);

    const handleChange = (event) => {
        let newValue = event.target.value;

        if(newValue !== '' && newValue < 1)
            newValue = 1;

        newValue = Math.round(newValue);

        if(newValue != value) {
            setValue(newValue);
            setLevel(newValue);
        }
    };

    return (
        <NumericInput label="Level" value={value} onChange={handleChange}/>
    )
};

const AfkInput = ({afkHours, setAfkHours}) => {
    const [hours, setHours] = React.useState(afkHours)

    const handleChange = (event) => {
        let newValue = event.target.value;

        if(newValue !== '' && newValue <= 0)
            newValue = 0.5;

        if(newValue != hours) {
            setHours(newValue);
            setAfkHours(newValue);
        }
    };

    return (
        <NumericInput label="AFK Hours" value={hours} increment={0.5} onChange={handleChange} />
    )
};

const ComputePartialsInput = ({computePartials, setComputePartials, labelPlacement}) => {
      return (
          <FormControlLabel
              className='partialGoods'
              label="Compute partial goods?"
              labelPlacement={labelPlacement || "start"}
              control={(
                  <Switch
                      style={{color: "wheat"}}
                      color='warning'
                      checked={computePartials}
                      onChange={() => setComputePartials(!computePartials)} />
              )}
          />
      )
};
